<template>
  <div class="w_h100 index_page_box size0">
    <div class="loading_page" v-if="!load_after"></div>
    <div class="bottom_box" v-if="load_after">
      <div class="lottery_btn size20 mb20" @click="go_page('/lottery')">点击抽奖</div>
      <div class="active_info" @click="go_page('/rule')">
        <span class="size12">活动规则</span>
        <img class="w10 mt5" src="../../static/index/idx_down_arrow.png">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      load_after: false,
      imgList: [
        "./static/index/idx_bg.jpg",
        "./static/index/idx_down_arrow.png",
        "./static/index/active_rule.jpg"
      ]
    };
  },
  created() {
    var that = this,
      imgList = this.imgList;
    if (!this.load_after) {
      let count = 0;
      for (var i in imgList) {
        let img_ = new Image();
        img_.src = imgList[i];
        img_.onload = function() {
          count++;
          if (count == imgList.length) {
            that.load_after = true;
          }
        };
      }
    }
  },
  methods: {
    go_page(url) {
      this.$router.push(url);
    }
  }
};
</script>
<style scoped>
.loading_page {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: #999;
}
.index_page_box {
  background-image: url("../../static/index/idx_bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.bottom_box {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.lottery_btn {
  color: #fff;
  background: #ff8401;
  padding: 6px 40px;
}
.active_info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}
</style>


